<template>
  <div class="order">
    <div class="top"></div>
    <van-nav-bar
      title="我的订单"
      left-text="返回"
      left-arrow
      :fixed="true"
      @click-left="back"
    />
    <div class="my-bg">
      <div
        class="bg"
        :style="{backgroundImage: `url(${bg})`}"
      >
      </div>
    </div>
    <div class="tab-box">
      <van-tabs
        v-model="tabIndex"
        color="#0c34ba"
        line-height="2px"
        :animated="true"
        :swipeable="true"
        title-active-color="#0c34ba"
        @change="toggleStatus"
      >
        <van-tab
          :title="v.title"
          v-for="(v,i) in tabItems"
          :key="i"
        >

          <div v-if="orderDatas.length > 0">
            <div
              class="tab-item"
              v-for="(item,index) in orderDatas"
              :key="index"
            >
              <div class="clearfix tab-item-box">
                <div class="fl num">{{item.orderId}}</div>
                <div class="fr text">
                  <div
                    class="conduct-box"
                    v-if="item.status == 1"
                    @click="confirmReceive(item,index)"
                  >确认收货</div>
                  <div
                    class="complete-box"
                    v-else
                  >
                    <div class="finished">已完成</div>
                    <div
                      class="remove"
                      @click="removeOrder(item.orderId,index)"
                    >
                      <van-icon
                        name="delete"
                        color="#999"
                        size="20"
                      ></van-icon>
                    </div>
                  </div>
                </div>
              </div>

              <div class="order-box">
                <div>
                  <div
                    class="clearfix cell-box"
                    v-for="(value,i) in item.product"
                    :key="i"
                  >
                    <div class="fl pro-img">
                      <img
                        class="auto-img"
                        :src="value.smallImg"
                        alt=""
                      >
                    </div>
                    <div class="fl pro-info">
                      <div class="text-box">
                        <div class="clearfix">
                          <div class="fl text-name">{{value.name}}</div>
                          <div class="fl text-rule">{{value.rule}}</div>
                        </div>
                        <div class="text-enname">{{value.enname}}</div>
                      </div>
                      <div class="price-box">
                        <div class="fl price">￥{{value.price}}</div>
                        <div class="fr count">{{value.count}}</div>
                      </div>
                    </div>
                  </div>

                </div>
                <div class="pro-box">
                  <div class="pro-boxing">
                    <div class="date">{{item.date | formatDate('yyyy-MM-dd hh:mm:ss')}}</div>
                    <div class="clearfix">
                      <div class="fl pro-count">共计{{item.count}}件商品</div>
                      <div class="fr pro-total">合计 ￥{{item.total}}</div>
                    </div>
                  </div>
                  <div class="circle-box left"></div>
                  <div class="circle-box right"></div>
                </div>
              </div>
            </div>
          </div>
          <div
            v-else
            class="empty-box"
          >
            <van-empty
              description="没有订单,去逛一逛！"
              image="https://img.yzcdn.cn/vant/custom-empty-image.png"
            >
              <van-button
                round
                type="info"
                class="bottom-button"
                @click="goMenu"
              >
                逛一逛
              </van-button>
            </van-empty>
          </div>

        </van-tab>

      </van-tabs>
    </div>

  </div>
</template>

<script>
import "../assets/less/order.less";
import { utils } from "../assets/js/Utils";
export default {
  data() {
    return {
      tabIndex: 0,
      orderDatas: [],
      tabItems: [{ title: "全部" }, { title: "进行中" }, { title: "已完成" }],
      bg: "",
    };
  },
  created() {
     // 获取token字符串
      let tokenString = localStorage.getItem("TK");
      if (!tokenString) {
        setTimeout(() => {
          this.$toast("请先登录");
          return this.$router.push({name:'Login'});
        }, 1000);
      }
    // 获取用户背景图
    this.getBg();
    // 获取订单数据
    this.getOrderData(0);
  },

  methods: {
    // 返回
    back() {
      this.$router.go(-1);
    },

    // 回到菜单
    goMenu() {
      this.$router.push({ name: "Menu" });
    },

    // 获取用户背景图片
    getBg() {
      // 获取token字符串
      let tokenString = localStorage.getItem("TK");
      if (!tokenString) {
        return;
      }

      this.axios({
        method: "GET",
        url: "/findMy",
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == "A001") {
            this.bg = result.data.result[0].userBg;
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    // 获取订单数据
    getOrderData(status) {
      // status: 订单状态
      // 获取token字符串
      let tokenString = localStorage.getItem("TK");
      if (!tokenString) {
        setTimeout(() => {
          this.$toast("请先登录");
          return this.$router.go(-1);
        }, 1000);
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/findOrder",
        params: {
          appkey: this.appkey,
          tokenString,
          status,
        },
      })
        .then((result) => {
          this.orderDatas = [];
          this.$toast.clear();
          //   {
          //       订单号：{
          //           date: '',
          //           count:0,
          //           total:0,
          //           status:0
          //           商品数据:[{},{}]
          //       }
          //   }

          if (result.data.code == "70000") {
            let orderData = {};
            result.data.result.map((v) => {
              if (!orderData[v.oid]) {
                //   如果不存在该订单数据，则初始化一个对象
                orderData[v.oid] = {
                  orderId: v.oid,
                  date: v.createdAt,
                  count: v.count,
                  total: v.count * v.price,
                  status: v.status,
                  product: [v],
                };
              } else {
                orderData[v.oid].count += v.count;
                orderData[v.oid].total += v.count * v.price;
                orderData[v.oid].product.push(v);
              }
            });

            for (let key in orderData) {
              this.orderDatas.push(orderData[key]);
            }
            this.orderDatas.sort((a, b) => {
              return new Date(b.date).getTime() - new Date(a.date).getTime();
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    // 切换订单状态
    toggleStatus(name, title) {
      this.getOrderData(name);
    },

    // 确认收货
    confirmReceive(item, index) {
      this.$dialog
        .confirm({
          title: "是否确认收货",
          message: "一旦确认收货将无法修改！",
          confirmButtonColor: "#0c34ba",
        })
        .then(() => {
          // 订单号 item.orderId
          //   订单状态 item.status
          // 下标 index
          // 获取token字符串
          let tokenString = localStorage.getItem("TK");
          if (!tokenString) {
            return;
          }

          // 参数序列化
          let data = utils.queryString({
            appkey: this.appkey,
            tokenString,
            oid: item.orderId,
          });

          this.$toast.loading({
            message: "加载中...",
            forbidClick: true,
            duration: 0,
            loadingType: "spinner",
          });

          this.axios({
            method: "POST",
            url: "/receive",
            data,
          })
            .then((result) => {
              this.$toast.clear();

              if (result.data.code == "80000") {
                this.$toast(result.data.msg);
                if (this.tabIndex == 1) {
                  // 进行中
                  this.orderDatas.splice(index, 1);
                } else {
                  item.status = 2;
                }
              }
            })
            .catch((err) => {
              this.$toast.clear();
            });
        })
        .catch(() => {});
    },

    // 删除订单
    removeOrder(oid, index) {
      this.$dialog
        .confirm({
          title: "是否确认删除订单",
          message: "一旦删除将无法修改！",
          confirmButtonColor: "#0c34ba",
        })
        .then(() => {
          // 获取token字符串
          let tokenString = localStorage.getItem("TK");
          if (!tokenString) {
            return;
          }

          // 参数序列化
          let data = utils.queryString({
            appkey: this.appkey,
            tokenString,
            oid,
          });

          this.$toast.loading({
            message: "加载中...",
            forbidClick: true,
            duration: 0,
            loadingType: "spinner",
          });

          this.axios({
            method: "POST",
            url: "/removeOrder",
            data,
          })
            .then((result) => {
              this.$toast.clear();

              if (result.data.code == "90000") {
                this.$toast(result.data.msg);
                this.orderDatas.splice(index, 1);
              }
            })
            .catch((err) => {
              this.$toast.clear();
            });
        })
        .catch(() => {});
    },
  },
};
</script>

<style lang="less" scoped>
</style>